@font-face {
    font-family: 'Acumin Pro';
    src: url(./AcuminPro-Regular.woff2) format('woff2');
    font-weight: 400;
    font-style: normal;
    font-display: swap
}

@font-face {
    font-family: 'Acumin Pro';
    src: url(./AcuminPro-Italic.woff2) format('woff2');
    font-weight: 400;
    font-style: italic;
    font-display: swap
}

@font-face {
    font-family: 'Acumin Pro';
    src: url(./AcuminPro-Bold.woff2) format('woff2');
    font-weight: 700;
    font-style: normal;
    font-display: swap
}
